import { RegisterForm } from "../../components/register/RegisterForm";
import { IndexPageUrl, LoginPageUrl } from "../../utils/constants";
import { DomUtils } from "../../utils/domUtils";
import { AppUser } from "../../utils/AppUser";
var RegisterContent = /** @class */ (function () {
    function RegisterContent() {
    }
    RegisterContent.prototype.render = function () {
        var register = function (data) {
            var appUser = new AppUser();
            if (data.password === data.passwordCheck) {
                if (data.password.length >= 6) {
                    appUser.setUserInfo(data.email, data.password);
                    appUser.signUp(IndexPageUrl);
                }
                else {
                    window.alert("パスワードは6文字以上必要です。");
                }
            }
            else {
                window.alert("パスワードが間違っています。");
            }
        };
        var result = new Array;
        //ユーザー登録フォームを作成
        var form = new RegisterForm(register);
        var formElm = form.render();
        result.push(formElm);
        //ログインページへのリンクを作成
        var loginDiv = DomUtils.createElement("div");
        loginDiv.id = "login";
        var loginText = document.createTextNode("既にアカウントを持っている方は ");
        loginDiv.appendChild(loginText);
        var loginLink = DomUtils.createElement("a", [], "こちら");
        loginLink.href = LoginPageUrl;
        loginDiv.appendChild(loginLink);
        result.push(loginDiv);
        return result;
    };
    return RegisterContent;
}());
export { RegisterContent };
