import { SubmitButton } from './SubmitButton';
var Form = /** @class */ (function () {
    function Form(formId, submitButtonLabel, onSubmit) {
        this.form = document.createElement('form');
        this.form.id = formId;
        this.submitButton = new SubmitButton(submitButtonLabel, function (form) {
            var formData = new FormData(form);
            var entries = formData.entries();
            var data = Object.fromEntries(entries);
            onSubmit(data);
        });
    }
    Form.prototype.addInput = function (input) {
        this.form.appendChild(input.render());
    };
    Form.prototype.addInputs = function (inputs) {
        var _this = this;
        inputs.forEach(function (input) { return _this.addInput(input); });
    };
    Form.prototype.render = function () {
        this.form.appendChild(this.submitButton.render());
        return this.form;
    };
    Form.mailCheck = function (mail) {
        var mail_regex1 = new RegExp('(?:[-!#-\'*+/-9=?A-Z^-~]+\.?(?:\.[-!#-\'*+/-9=?A-Z^-~]+)*|"(?:[!#-\[\]-~]|\\\\[\x09 -~])*")@[-!#-\'*+/-9=?A-Z^-~]+(?:\.[-!#-\'*+/-9=?A-Z^-~]+)*');
        var mail_regex2 = new RegExp('^[^\@]+\@[^\@]+$');
        if (mail.match(mail_regex1) && mail.match(mail_regex2)) {
            // 全角チェック
            if (mail.match(/[^a-zA-Z0-9\!\"\#\$\%\&\'\(\)\=\~\|\-\^\\\@\[\;\:\]\,\.\/\\\<\>\?\_\`\{\+\*\} ]/)) {
                return false;
            }
            // 末尾TLDチェック（〜.co,jpなどの末尾ミスチェック用）
            if (!mail.match(/\.[a-z]+$/)) {
                return false;
            }
            return true;
        }
        else {
            return false;
        }
    };
    return Form;
}());
export { Form };
