import { rootDiv } from "./constants";
import { WebPage } from "../containers/Page/WebPage";
var PageUtils = /** @class */ (function () {
    function PageUtils() {
    }
    PageUtils.getPageType = function () {
        //クエリ文字列のパラメータ"page"の値を取得する
        var result;
        var pageQuery = PageUtils.getQuery("page");
        if ( //queryがPageTypeに合致するかどうか
        pageQuery === null ||
            pageQuery === "login" ||
            pageQuery === "register" ||
            pageQuery === "timetable" ||
            pageQuery === "task" ||
            pageQuery === "shift" ||
            pageQuery === "event" ||
            pageQuery === "calendar") {
            result = pageQuery;
            return result;
        }
        else {
            // 例外処理
            return;
        }
    };
    PageUtils.getQuery = function (paramName) {
        var pageUrl = window.location.href; //今開いているページのパス
        paramName = paramName.replace(/[\[\]]/g, "\\$&");
        var regex = new RegExp("[?&]" + paramName + "(=([^&#]*)|&|#|$)"), results = regex.exec(pageUrl);
        if (!results)
            return null;
        if (!results[2])
            return "";
        return decodeURIComponent(results[2].replace(/\+/g, " "));
    };
    PageUtils.matchQuery = function (paramName, value) {
        var pageQuery = PageUtils.getQuery(paramName);
        if (pageQuery === value) {
            return true;
        }
        else {
            return false;
        }
    };
    PageUtils.prototype.createPage = function () {
        var page = new WebPage(PageUtils.getPageType());
        var pageElm = page.render();
        rootDiv.appendChild(pageElm); // pageの要素をrootのdiv要素に追加する
    };
    return PageUtils;
}());
export { PageUtils };
