var DomUtils = /** @class */ (function () {
    function DomUtils(parentElement) {
        this.parentElement = parentElement;
    }
    /* ここから下はAIで用意した要るかどうかわからんメソッド共 */
    DomUtils.createElement = function (tagName, className, innerText) {
        var element = document.createElement(tagName);
        if (className) {
            if (typeof className === "string") {
                element.classList.add(className);
            }
            else if (typeof className === "object") {
                for (var i in className) {
                    if (className[i] === "")
                        break;
                    element.classList.add(className[i]);
                }
            }
        }
        if (innerText) {
            element.innerText = innerText;
        }
        return element;
    };
    DomUtils.createImg = function (src, alt, className) {
        var img = new Image();
        img.src = src;
        if (alt) {
            img.alt = alt;
        }
        if (className) {
            img.classList.add(className);
        }
        return img;
    };
    DomUtils.appendChild = function (parent, child) {
        parent.appendChild(child);
    };
    DomUtils.appendChildMultiple = function (parent, children) {
        children.forEach(function (child) {
            parent.appendChild(child);
        });
    };
    DomUtils.prototype.appendElement = function (tagName, className, innerText) {
        var element = DomUtils.createElement(tagName, className, innerText);
        if (this.parentElement)
            DomUtils.appendChild(this.parentElement, element);
    };
    DomUtils.prototype.appendImg = function (src, alt, className) {
        var img = DomUtils.createImg(src, alt, className);
        if (this.parentElement)
            DomUtils.appendChild(this.parentElement, img);
    };
    return DomUtils;
}());
export { DomUtils };
