import { Form } from "../../components/Ui/Form";
import { LoginForm } from "../../components/Login/LoginForm";
import { IndexPageUrl, RegisterPageUrl } from "../../utils/constants";
import { DomUtils } from "../../utils/domUtils";
import { AppUser } from "../../utils/AppUser";
var LoginContent = /** @class */ (function () {
    function LoginContent() {
    }
    LoginContent.prototype.render = function () {
        var login = function (data) {
            var appUser = new AppUser();
            appUser.setUserInfo(data.email, data.password);
            appUser.signIn(IndexPageUrl);
        };
        var result = new Array;
        //タイトルを作成
        var titleElm = DomUtils.createElement("div", "m-2", "ログイン");
        result.push(titleElm);
        //ログインフォームを作成
        var form = new LoginForm(login);
        var formElm = form.render();
        result.push(formElm);
        //ユーザー登録ページへのリンクを作成
        var registerLinkElm = DomUtils.createElement("a", [], "アカウントを作成");
        registerLinkElm.id = "register";
        registerLinkElm.href = RegisterPageUrl;
        result.push(registerLinkElm);
        var br0 = DomUtils.createElement("br");
        result.push(br0);
        //パスワードをリセット用リンクもどきを作成
        var passwordResetElm = DomUtils.createElement("a", [], "パスワードを忘れた場合");
        passwordResetElm.id = "passwordReset";
        passwordResetElm.addEventListener("click", function () {
            //パスワードリセットのメールを送るために、アカウントのメールアドレスを求める
            var email = window.prompt("パスワードをリセットするためのメールを送ります。\nアカウントのメールアドレスを入力してください。", "");
            if (Form.mailCheck(email)) { // 入力内容が正しいメールアドレス場合
                var appUser = new AppUser();
                appUser.resetEmail(email); // パスワードリセットのメールを送る
                window.alert("パスワードリセットのメールを送りました。");
            }
            else { // 空の場合やキャンセルした場合は警告ダイアログを表示
                window.alert("正しいメールアドレスを入力してください。");
            }
        });
        result.push(passwordResetElm);
        return result;
    };
    return LoginContent;
}());
export { LoginContent };
